body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, #cca4f6, #95baf9);
  color: #333;
}

.container {
  max-width: 500px;
  background: #fff;
  margin: 50px auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.container h2 {
  text-align: center;
  padding: 20px;
  font-size: 24px;
  background: #2575fc;
  color: #fff;
  margin: 0;
}

.form {
  padding: 20px;
}

.form div {
  margin-bottom: 15px;
}

.form label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #555;
}

.form input,
.form textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  transition: border-color 0.3s ease;
}

.form input:focus,
.form textarea:focus {
  border-color: #2575fc;
  outline: none;
  box-shadow: 0 0 5px rgba(37, 117, 252, 0.2);
}

.form textarea {
  height: 100px;
  resize: none;
}

.button {
  display: block;
  width: 100%;
  padding: 12px;
  font-size: 18px;
  color: #fff;
  background: #2575fc;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease;
  text-align: center;
}

.button:hover {
  background: #1a5cc2;
}

.alert {
  text-align: center;
  color: red;
  margin-top: 10px;
}

select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  color: #333;
  box-sizing: border-box;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

select:focus {
  border-color: #2575fc;
  outline: none;
  box-shadow: 0 0 5px rgba(37, 117, 252, 0.2);
}

option {
  font-size: 16px;
  color: #333;
  background-color: #fff;
}
